
















import Vue from 'vue';
import { useCvState } from '~/data/useCvState';

export default Vue.extend({
  props: {
    sectionName: {
      type: String,
      default: '',
    },
    displaySection: {
      type: Boolean,
      default: true,
    },
  },
  setup() {
    const { changeDisplaySection } = useCvState();
    return {
      changeDisplaySection,
    };
  },
});
